import { Component, OnInit, AfterContentInit, AfterViewInit, AfterViewChecked } from '@angular/core';
import { User } from 'src/app/Shared/Models/Authentication/User';
import { UserService } from '../../Authentication/user.service';
import { Observable, of } from 'rxjs';
import { CoreService } from '../../Services/core.service';
import { filter, take } from 'rxjs/operators';
import { NotFoundComponent } from '../../Pages/NotFound/NotFound.component';
import { ModalService } from '../../Services/modal.service';

declare const window: any;

@Component({
  selector: 'app-header',
  templateUrl: './app-header.component.html',
  styleUrls: ['./app-header.component.scss']
})
export class AppHeaderComponent implements OnInit , AfterViewChecked{
 

  public user:Observable<User> = new Observable();
  public auths : Observable<string[]>;
  public appVersion: Observable<string>;
  public mainLogoPath:Observable<string>;
  public headerCreated:Boolean;
  constructor(public userService:UserService, public coreService:CoreService, public modalService:ModalService) { 
    
    this.mainLogoPath = of("http://localhost:62119/Content/Client/img/ehesap-01.png");
  }

  ngOnInit(): void {
    this.user = this.userService.currentUser;
    this.auths = this.coreService.auths;
    this.appVersion = this.coreService.appVersion;
    
  }
  ngAfterViewChecked(): void {
    let inputs = {
      isMobile: false
    }

    if(!this.headerCreated){
      this.coreService.initialDataFetched.pipe(filter(val=>val==true),take(1)).subscribe(val=>{
        window.AngularIsReady();
    });
    this.headerCreated = true;
    }
   
  }

}
